<template>
  <div>
    <a-button type="primary" @click="addRoleShowBox">添加角色</a-button>
      <a-table rowKey="id" :columns="columns" :data-source="rolesList" :bordered="true" :pagination="false" childrenColumnName="">
        <!-- 这个插槽控制展开的数据 -->
    <template #expandedRowRender="{record}">
      <!-- 每一整行里面的空间进行8   16 平分 -->
      <a-row v-for="item1 in record.children" :key="item1.id" class="first-line">
          <a-col :span="4">
            <a-tag closable @close.prevent="deleteRightHandler(record.id,item1.id)" color="purple">{{item1.authName}}</a-tag><CaretRightOutlined />
         </a-col>
          <a-col :span="20">
            <a-row v-for="(item2,index) in item1.children" :key="index" :class="{'last-line':index!==0}">
                <a-col :span="8">
                  <a-tag closable @close.prevent="deleteRightHandler(record.id,item2.id)" color="blue">{{item2.authName}}</a-tag><CaretRightOutlined />
                  </a-col>
                     <a-col :span="16">
                       <span  v-for="item3 in item2.children" :key="item3.id">
                        <a-tag closable @close.prevent="deleteRightHandler(record.id,item3.id)" color="green">{{item3.authName}}</a-tag>
                       </span>
                     </a-col>
            </a-row>
          </a-col>

      </a-row>
    </template>
        <!-- 这个插槽控制索引号 -->
    <template #index="{index}">
      {{index}}
    </template>
    <!-- 这个插槽控制造作 -->
    <template #handle="{text}">

        <a-button type="primary" size="small" @click="showEditRolesBox(text.id)">
        <template #icon><EditOutlined /></template>
        编辑
        </a-button>

        <a-button type="primary" size="small" danger @click="deleteRoleHandler(text.id)">
        <template #icon><CloseOutlined /></template>
        删除
      </a-button>

        <a-button type="primary" size="small" @click="showSetRightBox(text.id)">
        <template #icon><ToolOutlined /></template>
        分配权限
      </a-button>

    </template>
  </a-table>
  </div>
        <!-- 编辑角色的表单容器 -->
           <a-modal v-model:visible="editVisible" width="700px" title="编辑角色" @ok="editRolesHandle">
                <p><span>角色名称 : </span><a-input v-model:value="editRolesList.roleName" style="width:80%"></a-input></p>
                <p><span>角色描述 : </span><a-input v-model:value="editRolesList.roleDesc" style="width:80%"></a-input></p>
          </a-modal>
        <!-- 修改用户权限的表单容器 -->
      <a-modal v-model:visible="visible" title="分配权限" @ok="setRightHandler">
          <a-alert style="margin-bottom:20px" message="注意:只能设置1,2,3级权限 !" type="warning" show-icon />
          <span>请输入权限:</span><a-input style="width:80%" v-model="assignRoles.rids"></a-input>
    </a-modal>
        <!-- 添加用户的表单容器 -->
    <a-drawer
    title="创建新角色"
    :width="720"
    :visible="addRoleShow"
    @close="onClose"
    :body-style="{ paddingBottom: '80px' }"
  >
    <a-form ref="formRef" :model="form" layout="vertical">
      <a-row :gutter="16">
        <a-col :span="24">
          <a-form-item label="角色名称" name="roleName">
            <a-input  v-model:value="form.roleName" placeholder="请输入角色名" />
          </a-form-item>
        </a-col>
      </a-row>
      <a-row :gutter="16">
        <a-col :span="18">
          <a-form-item   label="角色描述" name="roleDesc">
            <a-input v-model:value="form.roleDesc" placeholder="请输入角色描述" />
          </a-form-item>
        </a-col>
      </a-row>
    </a-form>
    <div
      :style="{
        position: 'absolute',
        right: 0,
        bottom: 0,
        width: '100%',
        borderTop: '1px solid #e9e9e9',
        padding: '10px 16px',
        background: '#fff',
        textAlign: 'right',
        zIndex: 1,
      }"
    >
      <a-button style="margin-right: 8px" @click="onClose">取消</a-button>
      <a-button type="primary" @click="submit">提交</a-button>
    </div>
  </a-drawer>
</template>

<script>
import { reactive, ref } from '@vue/reactivity'
import { getCurrentInstance, onMounted, createVNode } from 'vue'
import { message, Modal } from 'ant-design-vue'
import { EditOutlined, CloseOutlined, ToolOutlined, CaretRightOutlined, ExclamationCircleOutlined } from '@ant-design/icons-vue'
export default {
  components: {
    EditOutlined,
    ToolOutlined,
    CloseOutlined,
    CaretRightOutlined
  },
  setup () {
    const formRef = ref()
    const { proxy } = getCurrentInstance()
    // 定义表格的数据信息以及开放插槽
    const columns = [
      {
        title: '#',
        slots: {
          customRender: 'index'
        }

      },
      {
        title: '角色ID',
        dataIndex: 'id'
      },
      {
        title: '角色名称',
        dataIndex: 'roleName'
      },
      {
        title: '角色描述',
        dataIndex: 'roleDesc'
      },
      {
        title: '操作',
        slots: {
          customRender: 'handle'
        },
        className: 'td-center'
      }
    ]

    // 表格的数据源
    const rolesList = ref()
    // 获取角色列表数据
    const queryRoles = async () => {
      const { meta, data } = await proxy.$http.get('roles')
      if (meta.status != 200) return message.error(meta.msg)
      rolesList.value = data
    }
    onMounted(() => {
      queryRoles()
    })
    // 删除指定权限处理函数
    const deleteRightHandler = (roleID, rightID) => {
      Modal.confirm({
        title: () => '确定删除此权限?',
        icon: () => createVNode(ExclamationCircleOutlined),
        content: () => createVNode('div', { style: 'color:red;' }, '此操作不可撤回,谨慎删除 ! ! !'),
        async onOk () {
          const { meta } = await proxy.$http.delete(`roles/${roleID}/rights/${rightID}`)
          if (meta.status != 200) return message.error(meta.msg)
          message.success(meta.msg)
          queryRoles()
        },
        onCancel () {
          message.info('已取消删除')
        }
      })
    }
    // 定义控制分配权限容器显示与否的状态变量
    const visible = ref(false)
    // 定义添加角色盒子的显示与隐藏状态变量
    const addRoleShow = ref(false)
    const ID = ref(0)
    // 点击显示分配权限按钮显示盒子
    const showSetRightBox = (id) => {
      ID.value = id
      visible.value = true
    }
    // 分配权限弹框里面的值
    const assignRoles = ref({
      rids: ''
    })
    // 分配权限盒子提交数据
    const setRightHandler = async () => {
      const { meta } = await proxy.$http.post(`roles/${ID.value}/rights`, assignRoles.value)
      if (meta.status != 200) return message.error(meta.msg)
      message.success(meta.msg)
      visible.value = false
      assignRoles.rids = ''
    }
    // 树形空间区域
    // 控件数据源
    const rightCheckedKeys = ref([])
    const rightList = ref([])

    const form = reactive({
      roleName: '',
      roleDesc: ''
    })

    // 显示添加角色盒子显示
    const addRoleShowBox = async () => {
      addRoleShow.value = true
    }
    // 点击取消,关闭盒子
    const onClose = () => {
      addRoleShow.value = false
    }
    // 点击提交注册新角色
    const submit = async () => {
      const params = {
        roleName: form.roleName,
        roleDesc: form.roleDesc
      }
      const { meta } = await proxy.$http.post('roles', params)
      if (meta.status != 201) return message.error(meta.msg)
      message.success(meta.msg)
      addRoleShow.value = false
      // 提交完成自动清空表单
      formRef.value.resetFields()
      queryRoles()
    }
    // 删除角色操作函数
    const deleteRoleHandler = async (userID) => {
      const { meta } = await proxy.$http.delete('roles/' + userID)
      if (meta.status != 200) return message.error(meta.msg)
      message.success(meta.msg)
      queryRoles()
    }
    // 控制编辑角色弹窗的变量
    const editVisible = ref(false)
    // 保存编辑用户的ID
    const editRolesId = ref()
    // 保存编辑用户后台返回过来的数据
    const editRolesList = reactive({
      roleName: '',
      roleDesc: ''
    })

    // 显示编辑角色弹窗
    const showEditRolesBox = async (id) => {
      editRolesId.value = id
      // 根据ID查询角色
      const { data } = await proxy.$http.get(`roles/${id}`)
      editRolesList.roleName = data.roleName
      editRolesList.roleDesc = data.roleDesc
      editVisible.value = true
      editRolesId.value = id
    }
    // 编辑角色弹窗数据的提交
    const editRolesHandle = async () => {
      const { meta } = await proxy.$http.put(`roles/${editRolesId.value}`, editRolesList)
      if (meta.status != 200) return message.error(meta.msg)
      message.success('编辑成功')
      queryRoles()
      editVisible.value = false
    }
    return {
      submit,
      editVisible,
      editRolesHandle,
      editRolesList,
      showEditRolesBox,
      onClose,
      assignRoles,
      addRoleShow,
      addRoleShowBox,
      deleteRoleHandler,
      rolesList,
      columns,
      formRef,
      form,
      visible,
      rightList,
      rightCheckedKeys,
      deleteRightHandler,
      showSetRightBox,
      setRightHandler
    }
  }
}
</script>

<style lang="less" scoped>
.td-center{
  text-align: center !important;
}
.ant-btn-sm{
  font-size: 12px !important;
}
.ant-btn-dangerous{
  margin: 0 8px;
}
.first-line{
  display: flex;
  align-items: center;
  border-bottom: 1px solid #ccc;
}
.last-line{
  border-top: 1px solid #ccc;
}
.ant-tag{
  margin: 5px;
  padding: 4px;
}
</style>
<style lang="less">
.ant-table-expanded-row{
  text-align: left !important;
}
.td-center{
  text-align: center !important;
  width: 300px;
}
.ant-table-body table{
  text-align: center !important;
}
.ant-table-thead th{
  text-align: center !important;
}
</style>>
